<template>
    <div class="v-cart l-wrap">
        <h2>Wishlist</h2>
        <div class="v-cart__wrap" v-if="wishlistCheck.length > 0">
            <v-wish-item
                v-for="item in wishlistCheck"
                :key="item.article"
                :wishlist-item-data="item"
                @remove="remove"

            />
        </div>
        <div class="v-cart__empty" v-else>
            <div class="v-cart-item border">
                Wishlist is empty
                <div class="test test-1"></div>
                <div class="test test-2"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import VWishItem from '../../components/wish/VWishItem';
    import {mapActions, mapGetters} from 'vuex';

    export default {
        components: {
            VWishItem
        },
        data: () => ({}),
        mounted() {},
        computed: {
             ...mapGetters('system', [
                'getWishlist',
                'getProducts',
                'wishlistCheck'
            ])
        },
        methods: {
            ...mapActions('system', {
                remove(dispatch, data) {
                    dispatch('spliceFromWishlist', data)
                }
            })
        }
    };
</script>

