<template>
    <div class="v-cart-item border">
        <div class="v-cart-item__wrap">
            <picture class="v-img">
                <img
                    src="https://picsum.photos/50/50?v=${_uid}`"
                >
            </picture>
            <h3 class="v-cart-item__title">
                <router-link class="v-card-link" :to="'/product/' + cartItemData.article">
                    {{ cartItemData.title }}
                </router-link>
            </h3>
            <p class="v-cart-item__text">
                Price: {{ cartItemData.price }}
            </p>
            <div class="v-cart-item__quantity">
                <span class="v-cart-item__quantity-text">
                    Quantity:
                </span>
                <span class="btn v-cart-item__quantity-counter">
                    <span
                        @click="decrement"
                        class="v-cart-item__quantity-item"
                    >
                        -
                    </span>
                    {{ cartItemData.quantity }}
                    <span
                        @click="increment"
                        class="v-cart-item__quantity-item"
                    >
                        +
                    </span>

                </span>
            </div>
            <button
                class="btn btn-dark v-cart-item__delete"
                @click="remove"
            >
                 <span class="material-icons">close</span>
            </button>
        </div>
    </div>
</template>

<script>
    export default {
        components: {},
        props: {
            cartItemData: {
                type: Object,
                default() {
                    return {};
                }
            }
        },
        data: () => ({}),
        computed: {},
        mounted() {
            // this.$set(this.cartItemData, 'quantity', '1');
        },
        methods: {
            decrement() {
                this.$emit('decrement', this.cartItemData.article)
            },
            increment() {
                this.$emit('increment', this.cartItemData.article)
            },
            remove() {
                this.$emit('remove', this.cartItemData.article)
            }
        }
    };
</script>
