import Vue from 'vue';
import VueRouter from 'vue-router';

import CatalogPage from '../views/catalog/CatalogPage';
import CartPage from '../views/cart/CartPage';
import MainPage from '../views/main/MainPage';
import ProductPage from '../views/product/ProductPage';
import WishPage from '../views/wish/WishPage';
import PaymentPage from '../views/payment/PaymentPage';

Vue.use(VueRouter);

const router = new VueRouter({
    routes: [
        {
            path: '/',
            name: 'main',
            component: MainPage
        },
        {
            path: '/catalog',
            name: 'catalog',
            component: CatalogPage
        },
        {
            path: '/cart',
            name: 'cart',
            component: CartPage,
            props: true
        },
        {
            path: '/product/:article',
            name: 'product',
            component: ProductPage
        },
        {
            path: '/wishlist',
            name: 'wishlist',
            component: WishPage
        },
        {
            path: '/payment',
            name: 'payment',
            component: PaymentPage
        }
    ]
});

export default router;
