<template>
    <div class="v-cart l-wrap">
        <h2>Cart</h2>
        <div class="v-cart__wrap" v-if="cartCheck.length > 0">
            <v-cart-item
                v-for="item in cartCheck"
                :key="item.article"
                :cart-item-data="item"
                @increment="increment"
                @decrement="decrement"
                @remove="remove"

            />
            <div class="v-cart-item border">
                <div class="v-cart__total">
                    <p class="v-cart__total-title"> Total: </p>
                    <p class="v-cart__total-count">{{ cartTotalCost }}</p>
                </div>
            </div>
            <div class="v-cart__buy">
                <router-link :to="{ name: 'payment' }" class="btn">
                    Confirm and pay
                </router-link>
            </div>
        </div>
        <div class="v-cart__empty" v-else>
            <div class="v-cart-item border">
                Cart is empty
                <div class="test test-1"></div>
                <div class="test test-2"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import VCartItem from '../../components/cart/VCartItem';
    import {mapActions, mapGetters} from 'vuex';

    export default {
        components: {
            VCartItem
        },
        data: () => ({}),
        mounted() {},
        computed: {
             ...mapGetters('system', [
                'getCart',
                'getProducts',
                'cartCheck'
            ]),
            cartTotalCost () {
                let result = []

                if(this.cartCheck.length) {
                    for(let item of this.cartCheck) {
                        result.push(item.price * item.quantity)
                    }

                    result = result.reduce((sum, el) => {
                        return sum + el
                    })
                }

                return result
            }
        },
        methods: {
            ...mapActions('system', {
                increment(dispatch, data) {
                    dispatch('incrementCartItem', data)
                },
                decrement(dispatch, data) {
                    dispatch('decrementCartItem', data)
                },
                remove(dispatch, data) {
                    dispatch('spliceFromCart', data)
                }
            })
        }
    };
</script>

