<template>
    <div
        class="form-group"
        :class="{'is-fixed': fixed}"
    >
        <input
            :id="fieldId"
            :type="type"
            :value="value"
            :class="{ 'is-invalid': errors }"
            :placeholder="placeholder"
            v-model="input"
            :name="name"
            class="form-control form-control-lg btn"
            @focus="fixed = true"
            @blur="inputCheck"
            v-mask="tel"
            required
        >

        <div
            v-if="errors"
            class="invalid-feedback"
        >
            Please, enter correect name
        </div>
        <label
            v-if="label"
            :for="fieldId"
            class="form-group-label"
        >{{ label }}</label>
    </div>
</template>

<script>
    export default {
        components: {},
        props: {
            id: {
                type: String,
                default: ''
            },
            label: {
                type: String,
                default: null
            },
            name: {
                type: String,
                default: ''
            },
            placeholder: {
                type: String,
                default: ''
            },
            type: {
                type: String,
                default: 'text'
            },
            value: {
                required: true,
                default: ''
            },
            errors: {
                type: Boolean,
                default: false
            },
            tel: {
                type: String,
                default: ''
            }
        },
        data: () => ({
            input: '+38',
            fixed: false
        }),
        created() {},
        watch: {},
        computed: {
            fieldId() {
                return this.$props.id || this.$props.name;
            }
        },
        methods: {
             inputCheck() {
                if(this.input.length > 0) {
                    this.fixed = true
                } else {
                    this.fixed = false
                }
            }
        }
    }
</script>
