<template>
    <div class="v-header">
        <div class="v-header__inner l-wrap">
            <router-link :to="{ name: 'main' }">
                <span class="material-icons">logo_dev</span>
            </router-link>
            <ul class="v-header__list">
                <li class="v-header__list-item">
                    <div class="v-search" :class="{'is-active': searchActive}">
                        <input
                            type="text"
                            class="btn v-search-input"
                            v-model="searchValue"
                            @focus="searchActive = true"
                        >
                        <span class="v-search-btn">
                            <span class="material-icons">
                                search
                            </span>
                        </span>
                    </div>
                    <div class="v-header-search" v-if="searchActive">
                        <ul class="v-header-search__list" v-if="products.length">
                            <li
                                class="v-header-search__list-item"
                                v-for="(product, index) in products"
                                :key="index"
                                @click="searchActive = false"
                            >
                                <picture class="v-img v-header-search__list-img">
                                    <img
                                        :src="`https://picsum.photos/300/300?v=${_uid}`"
                                    >
                                </picture>
                                <div class="v-header-search__list-text">
                                    <router-link class="u-link" :to="'/product/' + product.item.article">
                                        {{ product.item.title }}
                                    </router-link>
                                    <p>
                                        {{ product.item.price }}
                                    </p>
                                </div>
                            </li>
                        </ul>
                        <p class="v-header-search__empty" v-else>
                            No results
                        </p>
                    </div>
                    <transition name="fade">
                        <div class="v-overlay" v-if="searchActive" @click="searchActive = false"/>
                    </transition>
                </li>
                <li class="v-header__list-item">
                    <router-link :to="{ name: 'catalog', query: { q1: 'all' }}" @click="this.$router.push({ query: {q1: 'all'} })">
                        Catalog
                    </router-link>
                </li>
                <li class="v-header__list-item">
                    <router-link :to="{ name: 'wishlist', params: { wishlistData: getWishlist }}">
                        Wishlist
                        <span class="v-header__cart">
                            {{ getWishlist.length }}
                        </span>
                    </router-link>
                </li>
                <li class="v-header__list-item">
                    <router-link :to="{ name: 'cart', params: { cartData: getCart }}">
                        Cart
                        <span class="v-header__cart">
                            {{ getCart.length }}
                        </span>
                    </router-link>
                </li>
                <li class="v-header__list-item">
                    <button class="switch btn" id="theme-toggle" @click="changeMode"></button>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import Fuse from 'fuse.js'

    export default {
        components: {},
        props: {},
        data: () => ({
            searchActive: false,
            searchValue: ''
        }),
        watch: {},
        computed: {
            ...mapGetters('system', [
                'getCart',
                'getWishlist',
                'getProducts'
            ]),
            products() {
                const fuse = new Fuse(this.getProducts, {keys:['title']});

                return fuse.search(this.searchValue)
            }
        },
        mounted() {
            this.checkDarkMode();
        },
        methods: {
            ...mapActions('system', [
                'toggleDarkMode',
                'checkDarkMode'
            ]),
            changeMode() {
                this.toggleDarkMode();
            }
        }
    };
</script>
