<template>
    <div class="v-catalog l-wrap">
            <div class="v-catalog__cart">
                <div class="v-select">
                    <!-- <select class="btn" @change="queryFilter" v-model="filterValue" required>
                        <option value="all" disabled selected>Choose category</option>
                        <option value="1">Category 1</option>
                        <option value="2">Category 2</option>
                        <option value="3">Category 3</option>
                        <option value="4">Category 4</option>
                    </select> -->
                    <multiselect class="btn" @input="queryFilter" v-model="filterValue" track-by="title" label="title" :options="options" :searchable="false" :allow-empty="false"/>
                    <button v-if="this.$route.query.q1 !== 'all'" @click="clearFilter" class="v-select-btn">
                        <span class="material-icons">
                            close
                        </span>
                    </button>
                </div>
                <div class="v-select-range" style="width: 150px">
                    <vue-slider v-model="range" :max="1000"></vue-slider>
                </div>
                <router-link :to="{ name: 'cart' }" class="btn">
                    Cart: {{ getCart.length }}
                </router-link>
            </div>
        <div class="v-catalog__wrap">
            <v-catalog-item
                v-for="(product, index) in filter"
                :key="index"
                :product-data="product"
                @addToCart="addToCart"
                @addToWishlist="addToWishlist"
            />
        </div>
    </div>
</template>

<script>
    import VCatalogItem from '../../components/catalog/VCatalogItem';
    import {mapGetters, mapActions} from 'vuex';
    import Multiselect from 'vue-multiselect'
    import VueSlider from 'vue-slider-component'

    export default {
        components: {
            VCatalogItem,
            Multiselect,
            VueSlider
        },
        props: {},
        data: () => ({
            filterValue: {
                    title: 'Choose category',
                    value: 'all',
                    $isDisabled: true
            },
            range: [100, 900],
            options: [
                {
                    title: 'Choose category',
                    value: 'all',
                    $isDisabled: true
                },
                {
                    title: 'Category 1',
                    value: '1'
                },
                {
                    title: 'Category 2',
                    value: '2'
                },
                {
                    title: 'Category 3',
                    value: '3'
                },
                {
                    title: 'Category 4',
                    value: '4'
                }
            ]
        }),
        mounted() {
            this.queryCheck()
            window.onpopstate = () => {
                this.queryCheck()
            }
        },
        watch: {
            $route() {
                this.queryCheck()
            }
        },
        computed: {
            ...mapGetters('system', [
                'getProducts',
                'getCart'
            ]),
            filter() {
                if(this.$route.query.q1 === 'all') {
                    return this.getProducts.filter(item => item.price >= this.range[0] && item.price <= this.range[1])
                } else {
                    return this.getProducts.filter(item => item.category === this.$route.query.q1 && item.price >= this.range[0] && item.price <= this.range[1])
                }
            }
        },
        methods: {
             ...mapActions('system', [
                 'pushToCart',
                 'pushToWishlist'
             ]),
            addToCart(data) {
                this.pushToCart(data);
            },
            addToWishlist(data) {
                this.pushToWishlist(data);
            },
            clearFilter() {
                if(this.$route.query.q1 !== 'all') {
                    this.$router.push({ query: {q1: 'all'} })
                }
            },
            queryFilter() {
                this.$router.push({ query: {q1: this.filterValue.value} })
            },
            queryCheck() {
                this.filterValue = this.options.filter(item => item.value === this.$route.query.q1)
            },
            // rangeValue() {
            //     if(this.minPrice > this.maxPrice) {
            //         let tmp = this.maxPrice
            //         this.maxPrice = this.minPrice
            //         this.minPrice = tmp
            //     }
            // }
        }
    };
</script>
